'use client';

import type { LogItem, SessionInfo } from '@/types/logs';
import { useMemo } from 'react';

export type LogPanelProps = {
  sessions: SessionInfo[];
  logs: LogItem[];
  filteredLogs: LogItem[];
  selectedSession: SessionInfo | null;
  selectedSessionId: string | null;
  filterMode: 'all' | 'session';
  updatedAt: string | null;
  isLoading: boolean;
  error: string | null;
  onViewSessionLogs: () => void;
  onShowAllLogs: () => void;
};

const truncate = (text: string, max = 120) =>
  text.length > max ? `${text.slice(0, max)}…` : text;

const LogPanel = ({
  sessions,
  logs,
  filteredLogs,
  selectedSession,
  selectedSessionId,
  filterMode,
  updatedAt,
  isLoading,
  error,
  onViewSessionLogs,
  onShowAllLogs,
}: LogPanelProps) => {
  const formatter = useMemo(
    () =>
      new Intl.DateTimeFormat(undefined, {
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
      }),
    [],
  );
  const numberFormatter = useMemo(() => new Intl.NumberFormat(undefined), []);
  const formatTime = (value: string | number) => {
    const date = typeof value === 'string' ? new Date(value) : new Date(value);
    if (Number.isNaN(date.getTime())) return '时间未知';
    return formatter.format(date);
  };
  const activeLogs = filteredLogs;

  const headerMeta = isLoading
    ? '同步中…'
    : error
      ? `错误：${error}，确认 ~/.codex 可读`
      : updatedAt
        ? `更新于 ${formatTime(updatedAt)}`
        : '未更新';

  return (
    <aside className="log-panel" aria-live="polite" aria-busy={isLoading}>
      <header className="log-panel__header">
        <div>
          <p className="log-panel__eyebrow">Codex Farm</p>
          <h1 className="log-panel__title">日志面板</h1>
          <p className="log-panel__meta">{headerMeta}</p>
        </div>
        <div className="log-panel__stats">
          <div>
            <span className="log-panel__stat-label">会话</span>
            <strong className="log-panel__stat-value">
              {numberFormatter.format(sessions.length)}
            </strong>
          </div>
          <div>
            <span className="log-panel__stat-label">日志</span>
            <strong className="log-panel__stat-value">
              {numberFormatter.format(logs.length)}
            </strong>
          </div>
        </div>
      </header>

      <section className="log-panel__section">
        <div className="log-panel__section-head">
          <h2>选中猪</h2>
          <span className="log-panel__chip">
            {selectedSession ? (selectedSession.kind === 'main' ? '主会话' : '子代理') : '未选择'}
          </span>
        </div>
        {selectedSession ? (
          <div className="log-panel__detail">
            <p>
              <span>会话 ID</span>
              <strong>{selectedSession.id}</strong>
            </p>
            {selectedSession.parentId ? (
              <p>
                <span>父会话</span>
                <strong>{selectedSession.parentId}</strong>
              </p>
            ) : null}
            <p>
              <span>最近输入</span>
              <strong>
                {selectedSession.lastUserText
                  ? truncate(selectedSession.lastUserText, 90)
                  : '暂无用户输入'}
              </strong>
            </p>
          </div>
        ) : (
          <p className="log-panel__empty">点击场景中的猪以查看详情。</p>
        )}

        <div className="log-panel__actions">
          <button
            className="log-panel__button"
            type="button"
            onClick={onViewSessionLogs}
            disabled={!selectedSessionId}
            aria-pressed={filterMode === 'session'}
          >
            查看日志
          </button>
          <button
            className="log-panel__button log-panel__button--ghost"
            type="button"
            onClick={onShowAllLogs}
            aria-pressed={filterMode === 'all'}
          >
            显示全部
          </button>
        </div>
        <p className="log-panel__hint">方向键切换猪，WASD 移动农场主，Esc 取消选择。</p>
      </section>

      <section className="log-panel__section log-panel__section--logs">
        <div className="log-panel__section-head">
          <h2>最新 50 条</h2>
          <span className="log-panel__chip">
            {filterMode === 'session' && selectedSessionId ? '已过滤' : '全部'}
          </span>
        </div>

        {activeLogs.length === 0 ? (
          <p className="log-panel__empty">暂无日志，请稍后再看。</p>
        ) : (
          <ul className="log-panel__list">
            {activeLogs.map((item) => (
              <li key={item.id} className="log-panel__item">
                <div className="log-panel__item-head">
                  <span className={`log-panel__role log-panel__role--${item.role ?? 'unknown'}`}>
                    {item.role ?? 'unknown'}
                  </span>
                  <time className="log-panel__time" dateTime={item.iso}>
                    {formatTime(item.ts)}
                  </time>
                </div>
                <p className="log-panel__text">{truncate(item.text, 180)}</p>
                {item.sessionId ? (
                  <span className="log-panel__session">{item.sessionId}</span>
                ) : null}
              </li>
            ))}
          </ul>
        )}
      </section>
    </aside>
  );
};

export default LogPanel;
