'use client';

import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import type { LogItem, LogsResponse, SessionInfo } from '@/types/logs';

type FilterMode = 'all' | 'session';

type UseLogsState = {
  logs: LogItem[];
  sessions: SessionInfo[];
  selectedSessionId: string | null;
  filterMode: FilterMode;
  updatedAt: string | null;
  isLoading: boolean;
  error: string | null;
  filteredLogs: LogItem[];
  selectedSession: SessionInfo | null;
  selectSession: (sessionId: string | null) => void;
  setFilterMode: (mode: FilterMode) => void;
  refresh: () => Promise<void>;
};

type UseLogsOptions = {
  initialSessionId?: string | null;
  initialFilterMode?: FilterMode;
};

export const useLogs = (options: UseLogsOptions = {}): UseLogsState => {
  const [logs, setLogs] = useState<LogItem[]>([]);
  const [sessions, setSessions] = useState<SessionInfo[]>([]);
  const [updatedAt, setUpdatedAt] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedSessionId, setSelectedSessionId] = useState<string | null>(
    options.initialSessionId ?? null,
  );
  const [filterMode, setFilterMode] = useState<FilterMode>(
    options.initialFilterMode ?? 'all',
  );
  const isMounted = useRef(true);

  const refresh = useCallback(async () => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/logs', { cache: 'no-store' });
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}`);
      }
      const data = (await response.json()) as LogsResponse;
      if (!isMounted.current) return;
      setLogs(data.items ?? []);
      setSessions(data.sessions ?? []);
      setUpdatedAt(data.updatedAt ?? null);
    } catch (err) {
      if (!isMounted.current) return;
      const message = err instanceof Error ? err.message : '未知错误';
      setError(message);
    } finally {
      if (isMounted.current) setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    let alive = true;

    const run = async () => {
      await refresh();
      if (!alive) return;
    };

    run();
    const intervalId = window.setInterval(() => {
      if (alive) {
        void refresh();
      }
    }, 10000);

    return () => {
      alive = false;
      isMounted.current = false;
      window.clearInterval(intervalId);
    };
  }, [refresh]);

  const selectSession = useCallback((sessionId: string | null) => {
    setSelectedSessionId(sessionId);
    if (!sessionId) {
      setFilterMode('all');
    }
  }, []);

  const selectedSession = useMemo(() => {
    if (!selectedSessionId) return null;
    return sessions.find((session) => session.id === selectedSessionId) ?? null;
  }, [selectedSessionId, sessions]);

  const filteredLogs = useMemo(() => {
    if (filterMode !== 'session' || !selectedSessionId) {
      return logs;
    }
    return logs.filter((item) => item.sessionId === selectedSessionId);
  }, [filterMode, logs, selectedSessionId]);

  return {
    logs,
    sessions,
    selectedSessionId,
    filterMode,
    updatedAt,
    isLoading,
    error,
    filteredLogs,
    selectedSession,
    selectSession,
    setFilterMode,
    refresh,
  };
};
