'use client';

import dynamic from 'next/dynamic';
import { useSearchParams } from 'next/navigation';
import styles from './page.module.css';
import LogPanel from '@/components/LogPanel';
import { useLogs } from '@/hooks/useLogs';

const FarmScene = dynamic(() => import('@/components/FarmScene'), {
  ssr: false,
  loading: () => <div className={styles.sceneFallback}>3D 场景加载中…</div>,
});

export default function Home() {
  const searchParams = useSearchParams();
  const initialSessionId = searchParams.get('session');
  const initialFilter =
    searchParams.get('filter') === 'session' && initialSessionId ? 'session' : 'all';
  const {
    logs,
    sessions,
    filteredLogs,
    selectedSession,
    selectedSessionId,
    filterMode,
    updatedAt,
    isLoading,
    error,
    selectSession,
    setFilterMode,
  } = useLogs({
    initialSessionId,
    initialFilterMode: initialFilter,
  });

  return (
    <div className={styles.page}>
      <a className={styles.skipLink} href="#main">
        跳到内容
      </a>
      <main id="main" className={styles.scene}>
        <FarmScene
          sessions={sessions}
          selectedSessionId={selectedSessionId}
          onSelectSession={selectSession}
        />
      </main>
      <LogPanel
        sessions={sessions}
        logs={logs}
        filteredLogs={filteredLogs}
        selectedSession={selectedSession}
        selectedSessionId={selectedSessionId}
        filterMode={filterMode}
        updatedAt={updatedAt}
        isLoading={isLoading}
        error={error}
        onViewSessionLogs={() => {
          if (selectedSessionId) setFilterMode('session');
        }}
        onShowAllLogs={() => {
          setFilterMode('all');
        }}
      />
    </div>
  );
}
